.category-body-container {
  .category-body-top {
    font-size: 14rem;
    img {
      width: 200px;
      height: 50px;
      margin-right: 10px;
    }
    .title-container {
      display: flex;
      align-items: center;
    }
    .brand-name {
      font-size: 16rem;
    }
    .read-all {
      color: #fd7e14;
      font-size: 12rem;
      margin: 0px 10px;
      cursor: pointer;
    }
    .content {
      font-size: 12rem;
      margin: 30px 0;
    }
    .category-hr {
      border: none;
      height: 5px;
    }
  }

  .category-body-bot {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    cursor: pointer;
    img {
      &:hover {
        transform: scale(1.1);
        transition: 0.4s;
      }
    }

    .categoryName {
      &:hover {
        color: #fd7e14;
      }
    }
  }
}
